<template lang="pug">
  #modal-error.modal(:class="{ 'is-active': !!text }")
    .modal-background
    .modal-content
      .notification.is-danger
        .heading.has-text-centered Assignment error
        .has-text-centered {{ text }}
    button.modal-close(@click="close")
</template>

<script>
/**
 * @fires close
 */
export default {
  name: 'assignments',

  data() {
    return {
    };
  },

  props: {
    text: {
      type: String,
      default: '',
    },
  },

  methods: {
    /** @param {Event} e */
    close( e ) {
      this.$emit( 'close' );
    },
  },

  watch: {
    text( value ) {
      if ( value ) {
        console.error( 'MODAL_ERROR:', value );
      }
    },
  },
};
</script>



