<template lang="pug">
  #options(@click="close")
      .inner
        .options-title Settings
        .options(ref="container")
          //---- example of the content
          //- .group
          //-     .name [NAME]
          //-     .row
          //-         .label [LABEL]
          //-         select.value.ID
          //-             each ITEM in LIST
          //-                 option(value="#{ITEM}") #{ITEM}
          //-     .row
          //-         .label [LABEL]
          //-         input.value.id(type="checkbox")
        .buttons
          button.button.is-primary(@click="save") Save and close
          button.button.is-primary(@click="apply") Apply
          button.button.is-danger(@click="reset") Reset
</template>

<script>
import Colors from '@/vis/colors.js';
import { OptionsCreator, OptionGroup, OptionItem } from '@/vis/optionsCreator.js';

const ID = 'silko';

/**
 * @fires close
 * @fires apply
 */
export default {
  name: 'options',

  data() {
    return {
    };
  },

  props: {
    values: {
      type: Object,
      required: true,
    },
  },

  methods: {
    /** @param {Event} e */
    close( e ) {
      if ( e.target === this.$el ) {
        this.$emit( 'close' );
      }
    },

    /** @param {Event} e */
    save( e ) {
      this.saveSettings();
      this.update();

      this.$emit( 'apply' );
      this.$emit( 'close' );
    },

    /** @param {Event} e */
    apply( e ) {
      this.update();

      this.$emit( 'apply' );
    },

    /** @param {Event} e */
    reset( e ) {
      window.localStorage.removeItem( ID );

      OptionsCreator.restoreDefaults( this.values );

      this.$emit( 'apply' );

      const container = /** @type {Element} */ (this.$refs.container);
      container.innerHTML = '';
      this.bind();
    },

    loadSettings() {
      const options = JSON.parse( window.localStorage.getItem( ID ) );
      if ( !options ) {
        return;
      }

      const pop = /** @param {Object.<string>} storage; @param {Object.<string>} values */ ( storage, values ) => {
        for ( let name in storage ) {
          const value = values[ name ];
          const saved = storage[ name ];
          if ( !value ) {
            continue;
          }

          if ( typeof saved === 'object' ) {
            pop( saved, value );
          }
          else if ( typeof value.ref === 'function' ) {
            value.ref( saved );
          }
        }
      };

      pop( options, this.values );
    },

    saveSettings() {
      const options = {};

      const push = /** @param {Object.<string>} storage; @param {Object.<string>} values */ ( storage, values ) => {
        for ( let name in values ) {
          const value = values[ name ];
          if ( typeof value.ref === 'function' ) {
            storage[ name ] = value.ref();
          }
          else if ( typeof value === 'object' ) {
            storage[ name ] = { };
            push( storage[ name ], value );
          }
        }
      };

      push( options, this.values );

      window.localStorage.setItem( ID, JSON.stringify( options ) );
    },

    update() {
      for ( let valueID in this.values ) {
        const val = this.values[ valueID ];
        if ( typeof val.update === 'function' ) {
          val.update();
        }
      }
    },

    /** 
     * @param {string} activeVisID
     */
    show( activeVisID ) {
      const container = /** @type {Element} */ (this.$refs.container);
      const groups = Array.from( container.querySelectorAll( '.group' ) );
      groups.forEach( group => {
        const id = group.id;
        if ( id[0] === '_' || !activeVisID || id.indexOf( activeVisID ) === 0 ) {
          group.classList.remove( 'hidden' );
        }
        else {
          group.classList.add( 'hidden' );
        }
      } );
    },

    bind() {
      const container = /** @type {Element} */ (this.$refs.container);

      for ( let visID in this.values ) {
        const vis = /** @type {OptionGroup} */ (this.values[ visID ]);

        const group = document.createElement( 'div' );
        group.classList.add( 'group' );
        group.id = vis.id + '_group';

        const name = document.createElement( 'div' );
        name.classList.add( 'name' );
        name.textContent = vis.title;
        group.appendChild( name );

        if ( vis.options instanceof Array ) {
          vis.options.forEach( item => {
            const subGroup = this.createSubGroup( item, vis.id );
            group.appendChild( subGroup );
          } );
        }
        else {
          for ( let optionID in vis.options ) {
            const option = vis.options[ optionID ];
            const row = this.createRow( option, vis.id + '_' + optionID );
            group.appendChild( row );
          }
        }

        container.appendChild( group );
      }
    },

    /** 
     * @param {OptionGroup} sub
     * @param {string} visID
     * @returns {Element}
     */
    createSubGroup( sub, visID ) {
      const subgroup = document.createElement( 'div' );
      subgroup.classList.add( 'subgroup' );

      const name = document.createElement( 'div' );
      name.classList.add( 'subname' );
      name.textContent = sub.title;
      subgroup.appendChild( name );

      for ( let optionID in sub.options ) {
        const option = /** @type {Record<string, OptionItem>} */ (sub.options)[ optionID ];
        const row = this.createRow( option, visID + '_' + optionID );
        subgroup.appendChild( row );
      }

      return subgroup;
    },

    /** 
     * @param {OptionItem} option
     * @param {string} id
     * @returns {Element}
     */
    createRow( option, id ) {
      const row = document.createElement( 'div' );
      row.classList.add( 'row' );

      const label = document.createElement( 'div' );
      label.classList.add( 'row-label' );
      label.textContent = option.label;
      row.appendChild( label );

      if ( option.type === Array ) {
        row.appendChild( this.createSelect( option, id ) );
      }
      else if ( option.type === Boolean ) {
        row.appendChild( this.createCheckbox( option, id ) );
      }
      else if ( option.type === String ) {
        row.appendChild( this.createTextInput( option, id ) );
      }
      else if ( option.type === '#' ) {
        row.appendChild( this.createColorbox( option, id ) );
      }
      else if ( option.type === Number ) {
        row.appendChild( this.createNumberInput( option, id ) );
      }

      return row;
    },

    /** 
     * @param {OptionItem} option
     * @param {string} id
     * @returns {HTMLSelectElement}
     */
    createSelect( option, id ) {
      const select = document.createElement( 'select' );
      select.classList.add( 'value' );
      select.classList.add( id );

      const currentValue = option.ref();
      option.items.forEach( itemName => {
        const item = document.createElement( 'option' );
        item.value = itemName;
        item.textContent = itemName;
        if ( currentValue === itemName ) {
          item.selected = true;
        }
        select.appendChild( item );
      } );

      select.addEventListener( 'change', e => {
        option.ref( option.items[ /** @type {HTMLSelectElement} */ (e.target).selectedIndex ] );
      } );

      return select;
    },

    /** 
     * @param {OptionItem} option
     * @param {string} id
     * @returns {HTMLSpanElement}
     */
    createCheckbox( option, id ) {
      const container = document.createElement( 'span' );

      const checkbox = document.createElement( 'input' );
      checkbox.type = 'checkbox';
      checkbox.classList.add( id );

      checkbox.checked = option.ref();
      checkbox.addEventListener( 'click', e => {
        option.ref( /** @type {HTMLInputElement} */ (e.target).checked );
      } );

      const label = document.createElement( 'label' );
      const div = document.createElement( 'div' );
      div.textContent = '\u2714';

      label.appendChild( div );
      container.appendChild( checkbox );
      container.appendChild( label );

      return container;
    },

    /** 
     * @param {OptionItem} option
     * @param {string} id
     * @returns {HTMLInputElement}
     */
    createTextInput( option, id ) {
      const input = document.createElement( 'input' );
      input.type = 'text';
      input.classList.add( 'value' );
      input.classList.add( id );

      const val = option.ref();
      input.value = val;
      input.addEventListener( 'click', e => {
        option.ref( /** @type {HTMLInputElement} */ (e.target).value );
      } );

      return input;
    },

    /** 
     * @param {OptionItem} option
     * @param {string} id
     * @returns {HTMLInputElement}
     */
    createColorbox( option, id ) {
      const input = document.createElement( 'input' );
      input.type = 'color';
      input.classList.add( 'value' );
      input.classList.add( id );

      const val = option.ref();
      if ( val[0] === '#' ) {
        input.value = Colors.validateColor( val );
        input.addEventListener( 'change', e => {
          option.ref( /** @type {HTMLInputElement} */ (e.target).value );
        } );
      }
      else {
        const color = Colors.cssColorToHex( val );
        input.value = color.hex;
        input.setAttribute( 'alpha', color.a + '' );
        //input.alpha = color.a;
        input.addEventListener( 'change', e => {
          option.ref( Colors.hexToRgba( 
            /** @type {HTMLInputElement} */ (e.target).value, 
            +(/** @type {HTMLInputElement} */ (e.target).getAttribute( 'alpha' ) ) 
          ) );
        } );
      }

      return input;
    },

    /** 
     * @param {OptionItem} option
     * @param {string} id
     * @returns {HTMLInputElement}
     */
    createNumberInput( option, id ) {
      const number = document.createElement( 'input' );
      number.type = 'number';
      if ( option.step !== undefined ) {
        number.step = option.step + '';
      }
      if ( option.min !== undefined ) {
        number.min = option.min.toString();
      }
      if ( option.max !== undefined ) {
        number.max = option.max.toString();
      }
      number.classList.add( 'value' );
      number.classList.add( id );

      number.value = option.ref();
      number.addEventListener( 'change', e => {
        option.ref( +(/** @type {HTMLInputElement} */ (e.target).value) );
      } );

      return number;
    },

  },

  mounted() {
    this.loadSettings();
    this.bind();
  },
};
</script>

<style lang="less" scoped>
  #options {
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    z-index: 101;
    background-color: rgba(255, 255, 255, 0.35);

    .inner {
      font-size: 18px;
      font-family: Arial, sans-serif;
      max-width: 20em;
      max-height: 100%;
      margin: 0 0 0 auto;
      border: solid 1px #aaa;
      background-color: #ffe;
    }

    .options-title {
      font-size: 1.5em;
      font-family: 'Open Sans', sans-serif;
      padding: 4px;
      text-align: center;
    }

    .options {
      background-color: #eee;
      font-family: 'Roboto Condensed', sans-serif;
      max-height: ~"calc(100vh - 5em)";
      overflow: auto;
      border-top: solid 1px #ccc;
      border-bottom: solid 1px #ccc;
    }
  }
</style>

<style lang="less">
  #options {
    input,
    select {
      box-shadow: 2px 1px 2px 1px rgba(0, 0, 0, 0.1);
      border: solid 1px #ccc;
      border-radius: 5px;
    }

    select {
      appearance: none;

      background-image: url('data:image/png;base64,');
      background-repeat: no-repeat;
      background-position: right;
    }

    input[type="checkbox"] {
      opacity: 0;
      height: 1.5em;
      width: 1.5em;
      position: relative;
      right: -1.8em;
      z-index: 1;
    }

    input[type="checkbox"] + label {
      text-align: center;
    }

    input[type="checkbox"] + label div {
      user-select: none;
      display: inline-block;

      line-height: 1.5em;
      font-size: 0.75em;
      height: 1.5em;
      width: 1.5em;
      position: relative;
      top: -0.5em;

      margin: 0 4px 0 0;
      color: transparent;
      background-color: #fff;

      box-shadow: 2px 1px 2px 1px rgba(0, 0, 0, 0.1);
      border: solid 1px #ccc;
      border-radius: 5px;
    }

    input[type="checkbox"]:checked + label div {
      color: black;
    }

    input[type=number] {
      appearance: textfield;
    }

    .group {
      font-weight: bold;
      margin-bottom: 0.9em;

      &.inner {
        padding-left: 2em;
      }

      &.hidden {
        display: none;
      }
    }

    .name {
      color: #ffd;
      background: #444;
      margin-bottom: 0.5em;
      padding: 0.3em 0.5em;
      position: sticky;
    }

    .subname {
      margin: 0.3em 0.5em;
      border-bottom: solid 1px #bbb;
    }

    .row {
      font-size: 12pt;
      font-weight: normal;
      height: 1.8em;
      padding: 0.2em 0.5em;

      display: flex;
      flex-wrap: nowrap;
      justify-content: space-between;

      .cell {
        display: inline-block;

        span {
          padding: 0 0.2em;
        }
      }

      .row-label {
        display: inline-block;
        white-space: pre;
      }

      .value {
        width: 4em;
        text-align: right;
      }

      input.value[type="color"] {
        width: 4.5em;
      }

      select.value,
      input.value[type="text"] {
        width: 10em;
        text-align: left;
        height: 100%;
      }

      input.value[type="number"] {
        text-align: center;
      }

      input[type="radio"] {
        margin: 0;
        padding: 0;
        height: 24px;
      }
    }
  }

</style>



